.title{
  position: absolute;
  text-align: center;
  z-index: 1;
}

.title > p {
  left: 50%;
  transform: translate(-50%);
  font-size: 5rem;
  position: absolute;
  z-index: 1;
  white-space: nowrap;
}

.menu >div {
  width: 26rem;
  height: 100%;
}
.menu{
  width: 100%;
  height: 100%;
  position:absolute;
  display: flex;
  justify-content: center;
  align-items: center; 
  text-align: center; 
  font-family: Pretendard;
  font-size: 3.125rem;
  font-weight: bold;
  color: #fff;
}


.menu_01 {
  position: relative;
  
  
}
.menu_01_text{
  position: absolute;
